.nav_contents{
    display: flex;
    justify-content: space-between;
}

.nav{
    position: fixed;
    top:0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;

    /* Animations */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav_black{
    background-color: #111;
}

.nav_logo{
    position: fixed;
    top: 10px;
    left: 0;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
}

.nav_profile{
    position: fixed;
    right: 20px;
    width: 40px; 
    cursor: pointer;
    height: 40px;
    top: 15px; 
    border-radius: 30px;
}
